import React, { Component } from "react";
import './index.css';
import { push } from 'react-router-redux'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Card, Button, Container, Row, Col,Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import { NetworkApi } from '../../NetworkApi';
import "slick-carousel/slick/slick-theme.css";
import store from '../../store';
import {
    setCurrentProduct
  } from '../../modules/products'
const images = require.context('../../images', true);

function SampleNextArrow(props) {
    const { className, style, onClick } = props
    return (
        <div
            className={className}
            style={{ ...style, display: 'block', background: 'grey', borderRadius: '12px' }}
            onClick={onClick}
        ></div>
    );
}
function SamplePrevArrow(props) {
    const { className, style, onClick } = props
    return (
        <div
            className={className}
            style={{ ...style, display: 'block', background: 'grey', borderRadius: '12px' }}
            onClick={onClick}
        ></div>
    );
}

class Rating extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rating: this.props.rating || null,
            temp_rating: null,
            disabled: this.props.disabled
        };
    }
    rate(rating) {
        if (!this.state.disabled) {
            this.setState({
                rating: rating,
                temp_rating: rating,

            });
        }
    }
    star_over(rating) {
        if (!this.state.disabled) {
            this.state.temp_rating = this.state.rating;
            this.state.rating = rating;

            this.setState({
                rating: this.state.rating,
                temp_rating: this.state.temp_rating
            });
        }
    }
    star_out() {
        if (!this.state.disabled) {
            console.log(this)
            this.state.rating = this.state.temp_rating;

            this.setState({ rating: this.state.rating });
        }
    }
    render() {
        var stars = [];
        let _this = this;
        for (var i = 1; i <= 5; i++) {
            var klass = 'star-rating__star';

            if (_this.state.rating >= i && _this.state.rating != null) {
                klass += ' is-selected';
            }

            stars.push(
                <label
                    className={klass}
                    onClick={this.rate.bind(this, i)}
                    onMouseOver={this.star_over.bind(this, i)}
                    onMouseOut={this.star_out.bind(this)}>
                    ★
          </label>
            );
        }

        return (
            <div className="star-rating">
                {stars}
            </div>
        );
    }
};
class ItemDesc extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
          contactModal:false,
          recommended: "",
        };
    }

    componentDidMount() {
        if(Object.keys(this.props.activeProduct).length==0 && this.props.match.params && this.props.match.params.id){
            store.dispatch(setCurrentProduct(this.props.match.params.id));
        }
    }
    componentWillReceiveProps(nextProps){
      console.log(nextProps)

      if(Object.keys(nextProps.activeProduct).length==0)
          this.props.history.push('/');
    }
  

    fetchproductdata() {
        /*fetch('')
            .then((result) => {
                return result.json();
            }).then((jsonResult) => {
                this.setState({ "recommended": jsonResult });
                console.log(this.state.recommended);
            })
            .catch((error) => {
                console.log(error);
            });*/
    }

    toggle() {
      NetworkApi.getItemOwnerByItemId(1)
            .then((result) => {
              this.setState({
                contactInfo:result.data,
                contactModal: !this.state.contactModal
            });
            })
            .catch((error) => {
                console.log(error);
            });
            
    }

    render() {
        let product = [
            { "title": "Hamburger", "location": "Paris", "image": "./dress/dress1.png", "subcategory": "men", "price": "200" },
            { "title": "Fries", "location": "Venice", "image": "./dress/dress2.png", "subcategory": "women", "price": "100" },
            { "title": "Coke", "location": "New York", "image": "./dress/dress3.png", "subcategory": "men", "price": 50 },
            { "title": "Pepsi", "location": "Manchester", "image": "./dress/dress1.png", "subcategory": "men", "price": 50 }
        ];
        let settings = {
            arrows: true,
            nextArrow: <SampleNextArrow />,
            prevArrow: <SamplePrevArrow />,
            infinite: true,
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 1,
            responsive: [{ breakpoint: 1440, settings: { slidesToShow: 1, dots: true } },
            { breakpoint: 520, settings: { slidesToShow: 1, arrows: false, dots: true } }],
        };
        return (
            <div>
                <Container>
                <Modal isOpen={this.state.contactModal} toggle={this.toggle} className={this.props.className}>
                  <ModalHeader toggle={this.toggle}>Contact Information</ModalHeader>
                  <ModalBody>
                    {this.state.contactInfo}
                  </ModalBody>
                  <ModalFooter>
                    <Button color="secondary" onClick={this.toggle}>Close</Button>
                  </ModalFooter>
                </Modal>
                    <Row>
                        <Col>
                            <div>
                                <span><i class="fa fa-user mr-2"></i>Liel Beshari</span>
                                <span className="mt-2 star-rating__checkboxi"><Rating rating={2} /></span>
                            </div>
                            <Card className="w-75 mx-auto p-5">
                                <Slider {...settings}>
                                    {product && product.map((item, index) =>
                                        <Col>
                                            <Card>
                                                <img alt="product image" src={images(product[index].image, true)} className="c-i img-responsive p-3" />
                                                <span>
                                                    <h5 className="pull-left">{product[index].subcategory}</h5>
                                                    <i className="fa fa-heart fa-2x pull-right"></i>
                                                </span>
                                                <hr className="w-75" />
                                                <p>₪{product[index].price}</p>
                                            </Card>
                                        </Col>
                                    )}
                                </Slider>
                            </Card>
                            <Row className="mt-5 mx-auto">
                                <Col lg={6} md={6} sm={6} xs={6} className="pull-left">
                                    <p>{this.props.activeProduct.Item_Name}</p>
                                    <p>{this.props.activeProduct.Item_Category}</p>
                                    <p>{this.props.activeProduct.Item_Price}</p>
                                    <p>{this.props.activeProduct.Item_Country}</p>
                                    <p>{this.props.activeProduct.Item_Street_Number}</p>
                                </Col>
                                <Col lg={6} md={6} sm={6} xs={6}>
                                    <div onClick={this.toggle} className="pull-right cursor-pointer"><i class="fa fa-plus-circle"></i><span>Contact</span></div>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}

const mapStateToProps = state => ({
  activeProduct:state.product.activeProduct
})
const mapDispatchToProps = dispatch => bindActionCreators({
  changePage: () => push('/')
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ItemDesc)



