import React,{Component} from 'react'
import { push } from 'react-router-redux'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import SuccessOutlineButton from '../button/Button';
import { Container, Row, Col, Button } from "reactstrap";
import Card from "../card/Card";
import Slider from 'react-slick';
import { NetworkApi } from '../../NetworkApi';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import store from '../../store';
import {
  loadProducts,
  setCurrentProduct
} from '../../modules/products'
import {
    loadEvents
  } from '../../modules/events'
const images = require.context('../../images',true);
var products =  [
       {
           "Item_ID": 1,
           "Item_Name": "sample string 2",
           "Item_Price": 3,
           "Item_Size": "sample string 4",
           "Item_Category": 5,
           "Item_Subcategory": 6,
           "Item_Location": "sample string 7",
           "Item_Condition": "sample string 8",
           "Item_Image": "./dress/dress1.png",
           "Item_Description": "sample string 10"
       },
       {
           "Item_ID": 1,
           "Item_Name": "sample string 2",
           "Item_Price": 3,
           "Item_Size": "sample string 4",
           "Item_Category": 5,
           "Item_Subcategory": 6,
           "Item_Location": "sample string 7",
           "Item_Condition": "sample string 8",
           "Item_Image": "./dress/dress1.png",
           "Item_Description": "sample string 10"
       }
   ]



function SampleNextArrow(props) {
    const { className, style, onClick } = props
    return (
        <div
            className={className}
            style={{ ...style, display: 'block', background: 'grey', borderRadius: '12px' }}
            onClick={onClick}
        ></div>
    );
}

function SamplePrevArrow(props) {
    const { className, style, onClick } = props
    return (
        <div
            className={className}
            style={{ ...style, display: 'block', background: 'grey', borderRadius: '12px' }}
            onClick={onClick}
        ></div>
    );
}


let settings = {
    arrows: true,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [{ breakpoint: 1320, settings: { slidesToShow: 3 } },
      { breakpoint: 1024, settings: { slidesToShow: 2 } },
    { breakpoint: 767, settings: { slidesToShow: 1, arrows: false,dots: true, } }],
};
class Home extends Component{
  constructor(props){
    super(props);
    this.state={
      product:{}
    }
    this.redirectToTarget=this.redirectToTarget.bind(this);
  }
  componentDidMount(){
    /*store.dispatch({
      type:"product/LOAD_PRODUCTS",
      payload:products
    })*/
    console.log(this.props)
    store.dispatch(loadProducts());
    store.dispatch(loadEvents());
    
  }
  redirectToTarget(id){
    id=1;
    store.dispatch(setCurrentProduct(1));
    this.props.history.push('/item-desc/'+id)
  }
  render(){
    return(
            <div className="bg-light container-padding">
            <Container>
                <div className="pt-5 font-white ">
                    <h3 className="text-uppercase pl-8-5">Recommended for you</h3>
                    <p className="py-2 pl-8-5">we think you will love these picks</p>
                    <Container fluid={true}>
                            <Slider {...settings}>
                                {this.props.products && this.props.products.length>0 && this.props.products.map((item, index) =>
                                    <Col onClick={this.redirectToTarget}>
                                        <Card data-index={index} key={"p"+index}>
                                            <img alt="product image" src={this.props.products[index]['Item_Image']} className=" img-responsive c-i p-3" />
                                            <span>
                                                <h5 className="pull-left">{this.props.products[index].Item_Subcategory}</h5>
                                                <i className="fa fa-heart-o fa-2x pull-right"></i>
                                            </span>
                                            <hr className="w-75" />
                                            <p>₪{this.props.products[index].Item_Price}</p>
                                        </Card>
                                    </Col>
                                )}
                            </Slider>
                        <div class="d-flex justify-content-center mt-5 pb-5">
                            <Link className="button btn btn-secondary" to={'/'}>VIEW ALL</Link>
                        </div>
                    </Container>
                </div>

                <div className="pt-5 font-white ">
                    <h3 className="text-uppercase pl-8-5">Home Sale around you</h3>
                    <p className="py-2 pl-8-5">Discover </p>
                    <Container fluid={true}>
                            <Slider {...settings}>
                                {this.props.events && this.props.events.length>0 && this.props.events.map((item, index) =>
                                    <Col>
                                        <Card data-index={index} key={"ev"+index}>
                                            <img alt="product image" src={this.props.events[index]['Event_Image']} className="img-responsive c-i p-3" />
                                            <span>
                                                <h5 className="pull-left">{this.props.events[index].Event_Name}</h5>
                                                <i className="fa fa-heart-o fa-2x pull-right"></i>
                                            </span>
                                            <hr className="w-75" />
                                            <p>{this.props.events[index].Event_Country}</p>
                                        </Card>
                                    </Col>
                                )}
                            </Slider>
                        <div class="d-flex justify-content-center mt-5 pb-5">
                            <Link className="button btn btn-secondary" to={'/'}>VIEW ALL</Link>
                        </div>
                    </Container>
                </div>



                <div className="pt-5 font-white">
                    <h3 className="text-uppercase pl-8-5">Last Viewed</h3>
                    <p className="py-2 pl-8-5">Buy from your viewlist</p>
                    <Container>
                            <Slider {...settings}>
                                {this.props.products && this.props.products.map((item, index) =>
                                    <Col>
                                        <Card data-index={index} key={"lv"+index}>
                                            <img alt="product image" src={this.props.products[index]['Item_Image']} className="img-responsive c-i p-3" />
                                            <span>
                                                <h5 className="pull-left">{this.props.products[index].Item_Name}</h5>
                                                <i className="fa fa-heart-o fa-2x pull-right"></i>
                                            </span>
                                            <hr className="w-75" />
                                            <p>{this.props.products[index].Item_Location}</p>
                                        </Card>
                                    </Col>
                                )}
                            </Slider>
                        <div class="d-flex justify-content-center mt-5 pb-5">
                            <Link className="button btn btn-secondary"  to={'/'}>VIEW ALL</Link>
                        </div>
                </Container>
                </div>
                </Container>
            </div>
        );
      }
    }


const mapStateToProps = state => ({
  products:state.product.products,
  events:state.event.events
})
const mapDispatchToProps = dispatch => bindActionCreators({
  changePage: () => push('/about-us')
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home)



