import {NetworkApi} from '../NetworkApi';
export const LOAD_EVENTS = "event/LOAD_EVENTS";
const initialState = {
    events:[]
  }
  export default (state = initialState, action) => {
    console.log(state);
    switch (action.type) {
        case LOAD_EVENTS:
          return {
            ...state,
            events:action.payload
          }
  
      default:
        return state
    }
  }

export const loadEvents = () =>{
    //console.log(data);
   
    return dispatch => {
      dispatch({
        type:"global/START_LOADING"
      })
      return NetworkApi.getEvents().then(function(res){
        console.log(res.data);
        dispatch({
          type:LOAD_EVENTS,
          payload:res.data
        });
        dispatch({
          type:"global/STOP_LOADING"
        })
        return false;
      }).catch((error)=>{
              console.log("DELETE_ERROR: "+JSON.stringify(error));
              dispatch({
                type:LOAD_EVENTS,
                payload:[]
              });
              dispatch({
                type:"global/STOP_LOADING"
              })
              //dispatch(push('/delete-fail-uri'));
          });
    };
  }
  


