import {NetworkApi} from '../NetworkApi';
export const LOAD_PRODUCTS = "product/LOAD_PRODUCTS";
export const ACTIVE_PRODUCT = "product/ACTIVE_PRODUCT";

const initialState = {
  products:[],
  activeProduct:{}
}
export default (state = initialState, action) => {
  console.log(state);
  switch (action.type) {
      case LOAD_PRODUCTS:
        return {
          ...state,
          products:action.payload
        }
      case ACTIVE_PRODUCT:
      return {
        ...state,
        activeProduct:action.payload
      }
    default:
      return state
  }
}

export const setCurrentProduct = (id) => {
  return dispatch => {
    dispatch({
      type:"global/START_LOADING"
    })
    return NetworkApi.getItemDetailsById(id).then(function(res){
      console.log(res.data);
      dispatch({
        type:ACTIVE_PRODUCT,
        payload:res.data
      });
      dispatch({
        type:"global/STOP_LOADING"
      })
      return false;
    }).catch((error)=>{
            console.log("DELETE_ERROR: "+JSON.stringify(error));
            dispatch({
              type:LOAD_PRODUCTS,
              payload:[]
            });
            dispatch({
              type:"global/STOP_LOADING"
            })
            //dispatch(push('/delete-fail-uri'));
        });
  }
}

export const loadProducts = (data) =>{
  //console.log(data);
 
  return dispatch => {
    dispatch({
      type:"global/START_LOADING"
    })
    return NetworkApi.getItems().then(function(res){
      console.log(res.data);
      dispatch({
        type:LOAD_PRODUCTS,
        payload:res.data
      });
      dispatch({
        type:"global/STOP_LOADING"
      })
      return false;
    }).catch((error)=>{
            console.log("DELETE_ERROR: "+JSON.stringify(error));
            dispatch({
              type:LOAD_PRODUCTS,
              payload:[]
            });
            dispatch({
              type:"global/STOP_LOADING"
            })
            //dispatch(push('/delete-fail-uri'));
        });
  };
}



